@import './disclaimer';

.home-page {
    padding: 2rem;
    height: 100%;
    position: relative;

    &__popup{
        width: calc(100% - 4rem);
        background: #fff;
        border-radius: 1.75rem;        
        position: absolute;
        left: 50%;
        top: calc(50% - -7rem);
        transform: translate(-50%,-50%);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-flow: column;
        padding: 3em;

        transition: all .25s ease-in-out;

        &--hide {
            top: 100%;
            transform: translate(-50%, 0%);
        }

        &-title {
            color: $theme;
            font-size: 2.75rem;
            // padding: 1em 0;
            margin-bottom: 3rem;
        }

        &-text {
            color: $text-color-light;
            font-size: 1.5rem;
            text-align: center;

            margin-bottom: 1em;
        }

        .button {
            width: 100%;
            margin-top: 3rem;
        }
    }

    &__disclaimers{
        color:#fff;

        position: absolute;
        bottom: 3rem;
        right: 2rem;
        font-size: 1.5rem;
        z-index: 1;
    }

    
}