.question-choice-list {
    height: 100%;
    display: flex;
    flex-flow: column;

    &__question {
        font-size: 2rem;
        margin-bottom: 3rem;
        font-family: 'Noto Sans', sans-serif ;

    }

    &__options-container {
        overflow-y: auto;
        height: calc(100vh - 27rem);
        padding-right: 1rem;
    }

    &__options {
        height: 100%;
    }

    &__option{
        min-height: 6.5rem;
        width: 100%;
        border-radius: 1rem;
        border: 1px solid $border-color-light;
        font-size: 1.8rem;
        padding: 1rem 2rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        transition: all .25s ease-in-out;
        position: relative;

        label {
            max-width: calc(100% - 4rem);
        }

        img{
            height: 3rem;
            transform: translateX(-50%);            
        }

        &::after{
            content: '';
            width: 1.75rem;
            height: 1.75rem;
            border: 1px solid $border-color-light;
            border-radius: 50%;
        }

        &:not(:last-child){
            margin-bottom: 1.5rem;
        }

        &--selected{
            background-color: $theme;
            color: #fff;
            box-shadow: 0 0 15px -5px $theme;

            &::after{
                width: 1.5rem;
                height: 1.5rem;
                border: 3px solid $theme;
                background: #fff;
                box-shadow: 0 0 0 1px #fff;
            }

            img{
                filter: brightness(0) invert(1);
            }
        }

        &--with-icon {
            flex-flow: row-reverse;
            justify-content: flex-end;
            height: 10rem;

            &::after{
                margin-right: 4rem;
            }

            label {
                position: absolute;
                left: 12rem;                     
                padding-right: 2rem;       
            }
        }

        &--multiple {
            &::after{
                border-radius: 1px;
            }

            &.question-choice-list__option--selected{
                &::after{
                    border-width: 4px;
                }   
            }
        }
    }
}