.questions-nav{

    display: flex;
    align-items: center;
    justify-content: center;
    height: 8rem;



    &__dot {
        width: 1rem;
        height: 1rem;
        border-radius: 50%;
        background-color: rgba(#fff, .3);
        cursor: pointer;
        transition: background .25s ease-in-out;

        &:not(:last-child){
            margin-right: 1.75rem;
        }

        &--done {
            background-color: #fff;
        }

        &--current {
            background-color: transparent;
            box-shadow: 0 0 0 1px #fff;
            width: .9rem;
            height: .9rem;
        }
    }

}