.question-sorter{
    &__question {
        font-size: 2rem;
        margin-bottom: 3rem;
        font-family: 'Noto Sans', sans-serif ;

    }

    &__options-container {
        overflow-y: auto;
        height: calc(100vh - 27rem);
        padding-right: 1rem;
    }

    &__option{
        font-size: 1.8rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-left: 2rem;
        background: transparent;
        background: #fff;
        position: relative;
        z-index: 1;
        // transition: all .2s ease;

        &.dragging{
            z-index: 2;
            pointer-events: none;

            label{
                border:none;
                box-shadow: 0 0 9px -3px #969696;
            }
        }

        &:not(:last-child){
            margin-bottom: 1.5rem;
        }

        label {
            border: 1px solid $border-color-light;
            border-radius: 1rem;
            height: 6rem;
            display: flex;
            align-items: center;
            flex: 1;
            padding: 1rem 1rem 1rem 3rem;
            display: flex;
            align-items: center;
            justify-content: space-between;

            span {
                color: $theme;
                background-color: #eee;
                width: 3.5rem;
                height: 3.5rem;
                font-size: 1.25rem;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: .5rem;
                font-weight: bold;
            }
        }

        &-icon {
            height: 1px;
            width: 20px;
            background: #333;
            position: relative;
            margin-right: 2rem;
            
            &::after,&::before{
                content: '';
                height: 1px;
                width: 20px;
                background: #333;    
                position: absolute;
            }

            &::after{
                transform: translateY(-5px);
            }

            &::before{
                transform: translateY(5px);
            }
        }
    }
}