.closing-page {
    padding: 2rem;
    height: 100%;
    position: relative;

    &__popup{
        width: calc(100% - 4rem);
        background: #fff;
        border-radius: 1.75rem;        
        position: absolute;
        left: 50%;
        top: calc(50% - -7rem);
        transform: translate(-50%,-50%);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-flow: column;
        padding: 3em;

         

        &-title {
            color: $theme;
            font-size: 2.75rem;
            text-align: center;
            margin-bottom: 3rem;
        }

        &-text {
            color: $text-color-light;
            font-size: 1.5rem;
            text-align: center;

            margin-bottom: 1em;
        }

        .button {
            width: 100%;
            margin-top: 3rem;
        }
    }



    &__your-id {        
        text-align: center;
        line-height: 1;
        padding: 2rem 0;
        border-top: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
        width: 100%;
        margin-bottom: 3rem;

        h2{
            font-size: 2.5rem;
        }
        h1{
            color: $theme;
            font-size: 3.5rem;
        }
    }
    
}