.button {
    border: none;
    border-radius: .75rem;
    height: 5rem;
    padding: 0 1em;
    font-size: 1.35rem;
    transition: all .25s ease-in-out;
    font-weight: bold;
    outline: none;

    &--primary {
        background-color: $theme;
        color: #fff;
    }

    &--secondary {
        background-color: rgba($theme, .2);
        color: $theme;

    }

    &--outlined {
        border: 1px solid $border-color-light;
        background-color: #fff;
    }

    &--disabled {
        background-color: rgba($theme, .3);
    }
}