.arrow {
    width: 3rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    &--circle {
        border-radius: 50%;
    }

    &::after {
        content: '';       
        width: .7rem;
        height: .7rem;
        border-bottom: 1px solid;
        border-right: 1px solid;
        border-color: $theme;
    }

    &--left {
        &::after {
            transform: rotate(140deg) translate(-1px, -1px);
        }
    }
}