.disclaimer{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #fff;
    padding: 2rem;
    border-top-left-radius: 1.5rem;
    border-top-right-radius: 1.5rem;
    transform: translateY(100%);
    transition: all .25s ease-in-out;
    z-index: 2;

    &--show {
        transform: translateY(0%);
    }


    &__title {
        font-size: 2rem;
        margin: 1.5rem 0;
    }

    &__text {
        color: $text-color-light;
        font-size: 1.5rem;

        &--accept{
            color: rgba($theme, .8);
            margin: 5rem 0 6rem;
            position: relative;
            padding-left: 2.5rem;

            strong{
                font-weight: normal;
                text-decoration: underline;
            }

            &::before{
                content: '';
                width: 1.8rem;
                height: 1.8rem;
                border: 1px solid $theme;
                position: absolute;
                left: 0;
                border-radius: 2px;
            }        
            
        }

        &--checked {
            &::before{
                width: 1.7rem;
                height: 1.7rem;
                background-color: $theme;
                border-color: #fff;
                border-width: 3px;
                box-shadow: 0 0 0 1px $theme;
            }
        }
    }

    .button{
        width: 100%;
    }

}