.question-range-group{
    height: 100%;
    display: flex;
    flex-flow: column;

    &__question {
        font-size: 2rem;
        margin-bottom: 3rem;
        font-family: 'Noto Sans', sans-serif ;
    }

    &__items-container {
        overflow-y: auto;
        height: calc(100vh - 27rem);
        padding-right: 1rem;
    }

    &__item {
        border: 1px solid $border-color-light;
        border-radius: 1rem;

        label{
            padding: 2rem;
            display: block;
            font-size: 1.75rem;        
        }

        &:not(:last-child){
            margin-bottom: 1.5rem;
        }
    }

    &__control{
        display: flex;
        padding: 1rem 2rem;
        align-items: center;
        justify-content: space-between;
        border-top: 1px solid $border-color-light;

        &-button{
            border:1px solid $border-color-light;
            border-radius: 50%;
            width: 4rem;
            height: 4rem;
            background-color: #fff;

            &:last-child{
                position: relative;

                &::after, &::before{
                    position: absolute;
                    content: '';
                    width: 1.2rem;
                    height: 1.5px;
                    background: $theme;

                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    margin: auto;
                }

                &::before{
                    transform: rotate(90deg);
                }
            }

            &:first-child{
                position: relative;

                &::after{
                    position: absolute;
                    content: '';
                    width: 1.2rem;
                    height: 1.5px;
                    background: $theme;

                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    margin: auto;
                }
            }
        }

        h2{
            font-size: 1.75rem;
            font-family: 'Noto Sans', sans-serif ;

            span{
                display: inline-flex;
                min-width: 6rem;
                height: 3.5rem;
                background-color: #eee;
                align-items: center;
                justify-content: center;
                border-radius: .5rem;
                margin-right: 1rem;
                font-size: 1.65rem;
            }
        }
    }
}