@import './questions-nav';
@import './questions-panel';


.questionnaires-page{
    height: 100%;
    width: 100%;

    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: flex-start;


    &__buttons {
        background-color: #fff;
        margin-top: auto;
        padding: 2rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

        .button{
            // width: calc(50% - .5rem);
            &:first-child{
                margin-right: 1.5rem;
            }

            &:last-child{
                flex: 1;
            }
        }

        &--first-question {
            .button {
                width: 100%;
                flex: none;
                margin-right: 0 !important;
            }
        }
    }
}